import { HashRouter, Switch, Route } from "react-router-dom";
import Home from "./Components/Home";
import Test from "./Components/Test";

function App() {
  return (
    <HashRouter basename="/">
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/test" component={Test} />
      </Switch>
    </HashRouter>
  );
}

export default App;
